@import url(https://fonts.googleapis.com/css2?family=Karla:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=DM+Serif+Display&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-size: 16px;
}

body,
.App {
    width: 100vw;
    height: auto;
    font-family: 'Karla', sans-serif;
}
.different-container {
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 3rem 0;
}

.line {
    width: 40%;
    text-align: center;
    border-top: 1px solid hsl(273, 4%, 51%);
    margin-top: 2rem;
}

h2 {
    font-family: 'DM Serif Display', serif;
    letter-spacing: 1.2px;
    padding: 2rem 0;
    font-size: 2rem;
}

.content {
    width: 90%;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 1rem 0;
    text-align: center;
}

.content h4 {
    margin: 2rem 0;
}

.content img {
    max-width: 4rem;
}

.how-we-work {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    padding-bottom: 2rem;
    margin: 3rem 0;
    text-align: center;
    color: hsl(0, 0%, 98%);
    background-color: hsl(270, 9%, 17%);
    padding: 3rem 0;
}

.view-plans-btn {
    margin-bottom: 2rem;
}

.work-desktop {
    display: none;
}

.work-mobile {
    position: absolute;
    top: 0;
    right: 0;
}
.navbar {
    width: 100vw;
    height: auto;
    font-family: 'DM Serif Display', serif;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
}

.nav-menu,
.hamburger.active,
.close {
    display: none;
}

.hamburger,
.close.active {
    display: flex;
    width: 100%;
    justify-content: flex-end;
}

.nav-menu.active {
    width: 100%;
    height: 70vh;
    background-color: hsl(270, 9%, 17%);
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    padding-top: 2rem;
}

.nav-item {
    list-style-type: none;
    padding: 1rem 2rem;
}

.nav-item:hover {
    border: 2px solid hsl(0, 0%, 98%);
}

.nav-item a {
    color: hsl(0, 0%, 98%);
    text-decoration: none;
    text-transform: uppercase;
    font-size: 1.2rem;
    letter-spacing: 1.1px;
}

.bg-img {
    width: 100vw;
    height: 100%;
    background-image: url(/insure-page/static/media/bg-pattern-mobile-nav.b2fceba3.svg);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: bottom;
}
.work-container {
    width: 100vw;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.desktop-bg-img,
.desktop-img,
.desktop-bg-right-img {
    display: none;
}

.img-container {
    width: 100%;
    display: flex;
    flex-direction: column;
}

.text-container {
    width: 100%;
    height: 60vh;
    padding: 2rem;
    text-align: center;
    background-color: hsl(270, 9%, 17%);
    color: hsl(0, 0%, 98%);
    margin-top: -1rem;
    position: relative;
}

.mobile-bg-img {
    position: absolute;
    left: 0;
    top: 0;
}

h1 {
    font-size: 2.5rem;
    margin-bottom: 2rem;
}

.mobile-bg-right-img {
    position: absolute;
    right: 0;
    bottom: -11rem;
}

.view-plans-btn {
    background-color: hsl(270, 9%, 17%);
    color: hsl(0, 0%, 98%);
    padding: 1rem 2rem;
    border: 1px solid hsl(0, 0%, 98%);
    margin-top: 2rem;
    text-transform: uppercase;
    letter-spacing: 1.1px;
    cursor: pointer;
}

.view-plans-btn:hover {
    background-color: hsl(0, 0%, 98%);
    color: hsl(270, 9%, 17%);
}
